*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    display: flex;
    justify-content: center;
    align-items: center;
    /* height: 100vh; */
    background-color: #f1f2f6;
}
.box_container{
    margin-top: 6rem;
    background-color: #3498db;
    gap: 7rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 4rem 5rem;
    border-radius: 0.3rem;
}
.input_section{
    padding: 0.5rem;
    border-radius: 5rem;
    -webkit-border-radius: 5rem;
    -moz-border-radius: 5rem;
    -ms-border-radius: 5rem;
    -o-border-radius: 5rem;
    outline: none;
    border: none;
    letter-spacing: 0.8px;
    font-weight: bold;
}
::placeholder{
    padding-left: 1.2rem;
}

.city_name_container{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 1rem;
}
.wave{
    opacity: 0.3;
    position: absolute;
    top: 120%;
    left: 50%;
    background-color: white;
    width: 50rem;
    height: 50rem;
    margin-left: -25rem;
    margin-top: -20rem;
    transform-origin: 50% 48%;
    border-radius: 43%;
    -webkit-border-radius: 43%;
    -moz-border-radius: 43%;
    -ms-border-radius: 43%;
    -o-border-radius: 43%;
    animation: drift 3000ms infinite linear;
    -webkit-animation: drift 3000ms infinite linear;
    z-index:1;
}
.wave .three{
    animation: drift 5000ms infinite linear;
    -webkit-animation: drift 5000ms infinite linear;
    z-index: 2 !important;
    opacity: 0.2;
}
.wave .two{
    animation: drift 7000ms infinite linear;
    -webkit-animation: drift 7000ms infinite linear;
    z-index: 3 !important;
    opacity: 0.1;
}
@media(width<=450px)
{
    .tempmin_max{
        font-size: 1rem;
    }
}
